.apology-section {
	position: relative;
	padding: 5rem 0;
	color: #fff;
	@include max-md {
		padding: 3rem 0;
	}
	.title-section {
		h2 {
			color: #fff;
		}
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		border-radius: 15px;
		background-color: var(--color-primary);
		@include max-md {
			padding: 2rem 1rem;
		}
		.rose-bg-trans {
			position: absolute;
			left: 0;
			bottom: 0;
			height: 320px;
		}
	}
	.image-wrap {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		&::before {
			position: absolute;
			content: "";
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: rgba($color: #000000, $alpha: 0.4);
		}
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}
