@import "fonts", "mixins", "variables", "root", "animate", "countdown", "header.section", "couple.section", "story.section", "venue.section", "rsvp.section", "wishes.section", "egift.section", "gallery.section", "apology.section", "stream.section", "thank.section";

body {
	font-family: $font-primary;
	color: #fff;
}

.main-app {
	background-color: var(--color-main);
}

h1,
h2,
h3 {
	font-family: $font-secondary;
}

h1 {
	font-size: 52px;
	@include max-sm {
		font-size: 38px;
	}
}

h2 {
	margin-bottom: 0;
	font-weight: 500;
}

small {
	font-size: 12px;
}

@include max-sm {
	p {
		font-size: 14px;
	}
	small {
		font-size: 10px;
	}
}

.container {
	@include max-md {
		padding-left: 1.5rem;
		padding-right: 1.5rem;
	}
}

.moveable_section_wrapper {
	overflow-x: hidden;
	overflow-y: hidden;
}

.btn-custom {
	background-color: var(--color-secondary);
	padding: 0.5rem 2rem;
	transition: all 0.3s ease-in-out;
	// border-color: $color-primary;
	color: #fff;
	width: fit-content;
	margin: auto;
	border-radius: 10px;
	&:hover {
		color: #dadada;
		path {
			fill: #dadada;
		}
	}
}

.img-fluid {
	width: 100%;
	height: auto;
}

.font-primary {
	font-family: $font-primary;
}

.font-secondary {
	font-family: $font-secondary;
}

.form-control,
.form-select {
	font-family: $font-primary;
	border-color: rgba($color: #000000, $alpha: 0.3);
	padding: 0.5rem 1rem;
	background-color: #fff;
	&::placeholder {
		color: var(--color-dark);
	}
	&:focus {
		box-shadow: none;
		border-color: var(--color-dark);
	}
}

.scrolled {
	background: #fff !important;
	transition: all 1s ease-in-out;
	box-shadow: 3px 0px 15px 15px rgba(89, 89, 89, 0.2);
	visibility: visible !important;
	pointer-events: all !important;
	.logo-viding {
		path {
			fill: var(--vd-color-pink);
		}
	}
}

.navbar-hide {
	top: -70px;
}

.parallax-wrapper {
	overflow: hidden;
}

.progress {
	position: fixed;
	top: 0;
	left: 0;
	height: 2px;
	background-color: var(--vd-color-pink);
	width: 0;
	z-index: 9999;
}

.title-section {
	position: relative;
	width: 100%;
	margin: auto;
	text-align: center;
	margin-bottom: 1.25rem;
	text-transform: uppercase;
	&::before {
		position: absolute;
		content: "";
		background: url(../images/title-ornament.png) no-repeat center center/cover;
		width: 85px;
		height: -webkit-fill-available;
		top: 0;
		left: 50%;
		transform: translate(-50%, -100%);
	}
	h2 {
		background: var(--color-gradient);
		-webkit-background-clip: text;
		background-clip: text;
		-webkit-text-fill-color: transparent;
	}
	.deco-title {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
	}
}

.waves-bottom {
	position: absolute;
	width: 100%;
	left: 0;
	bottom: -2px;
	height: auto;
	z-index: 3;
	&.reverse {
		transform: scaleX(-1);
	}
	path {
		fill: var(--color-main);
	}
}
.waves-top {
	position: absolute;
	top: -2px;
	left: 0;
	width: 100%;
	height: auto;
	transform: scaleY(-1);
	path {
		fill: var(--color-main);
	}
}

.waves-pink-bottom {
	bottom: -50px;
	@include max-lg {
		bottom: -30px;
	}
	@include max-md {
		bottom: -15px;
	}
}

.waves-white-top,
.waves-pink-top {
	position: absolute;
	top: -2px;
	left: 0;
	width: 100%;
	transform: scale(-1);
}

.waves-pink-top {
	top: -50px;
	@include max-lg {
		top: -30px;
	}
	@include max-md {
		top: -15px;
	}
}

ul {
	padding: 0;
	li {
		padding: 0;
	}
}

@include min-md {
	.lg {
		display: block;
	}
	.sm {
		display: none;
	}
}
@include max-md {
	.lg {
		display: none;
	}
	.sm {
		display: block;
	}
}

.loader-wrapper {
	position: fixed;
	min-height: 100vh;
	background-color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	z-index: 1500;
	flex-direction: column;
	gap: 1.25rem;
	.spinner {
		position: relative;
		width: 84px;
	}
	span {
		color: var(--vd-color-pink);
		font-weight: 700;
		animation: fade 2s linear infinite;
		letter-spacing: 5px;
	}
}

@keyframes fade {
	0% {
		opacity: 0;
	}
	80% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

.modal.show-maps {
	.modal-content {
		border-radius: 20px;
	}
	.modal-body {
		padding: 2rem 3rem;
		position: relative;
		@include max-md {
			padding: 2rem 1rem;
		}
		.btn-modal {
			background-color: var(--color-main);
			color: var(--color-dark);
			position: absolute;
			right: 10px;
			top: 10px;
			border-radius: 50px;
			opacity: 0.5;
			padding: 0 0.425rem;
			transition: all 0.5s ease;
			&:hover {
				opacity: 1;
			}
		}
		.btn-custom {
			background: transparent;
			border-color: var(--color-dark);
			color: var(--color-dark);
			transition: all 0.25 ease-in-out;
			&:hover {
				background: var(--color-secondary);
				border-color: var(--color-secondary);
				color: var(--text-button);
			}
		}
		.maps-element {
			position: relative;
			width: 100%;
			aspect-ratio: 16/9;
			@include max-md {
				aspect-ratio: 9/10;
			}
		}
		.loader-wrapper-modal {
			display: block;
			width: 100%;
			height: 100%;
			background-color: transparent;
			display: flex;
			justify-content: center;
			align-items: center;
			&.loaded {
				display: none;
			}
			.lds-default {
				display: inline-block;
				position: relative;
				width: 80px;
				height: 80px;
			}
			.lds-default div {
				position: absolute;
				width: 6px;
				height: 6px;
				background: var(--color-secondary);
				border-radius: 50%;
				animation: lds-default 1.2s linear infinite;
			}
			.lds-default div:nth-child(1) {
				animation-delay: 0s;
				top: 37px;
				left: 66px;
			}
			.lds-default div:nth-child(2) {
				animation-delay: -0.1s;
				top: 22px;
				left: 62px;
			}
			.lds-default div:nth-child(3) {
				animation-delay: -0.2s;
				top: 11px;
				left: 52px;
			}
			.lds-default div:nth-child(4) {
				animation-delay: -0.3s;
				top: 7px;
				left: 37px;
			}
			.lds-default div:nth-child(5) {
				animation-delay: -0.4s;
				top: 11px;
				left: 22px;
			}
			.lds-default div:nth-child(6) {
				animation-delay: -0.5s;
				top: 22px;
				left: 11px;
			}
			.lds-default div:nth-child(7) {
				animation-delay: -0.6s;
				top: 37px;
				left: 7px;
			}
			.lds-default div:nth-child(8) {
				animation-delay: -0.7s;
				top: 52px;
				left: 11px;
			}
			.lds-default div:nth-child(9) {
				animation-delay: -0.8s;
				top: 62px;
				left: 22px;
			}
			.lds-default div:nth-child(10) {
				animation-delay: -0.9s;
				top: 66px;
				left: 37px;
			}
			.lds-default div:nth-child(11) {
				animation-delay: -1s;
				top: 62px;
				left: 52px;
			}
			.lds-default div:nth-child(12) {
				animation-delay: -1.1s;
				top: 52px;
				left: 62px;
			}
			@keyframes lds-default {
				0%,
				20%,
				80%,
				100% {
					transform: scale(1);
				}
				50% {
					transform: scale(1.5);
				}
			}
		}
		.modal-actions {
			position: relative;
			color: var(--color-dark);
		}
	}
}
