.egift-section {
	position: relative;
	text-align: center;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.background-gift {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 65%;
		content: "";
		background-color: var(--color-primary);
		border-radius: 50%/12%;
		@include max-md {
			border-radius: 50%/2%;
		}
	}
	.rose-bg-trans {
		position: absolute;
		right: 0;
		bottom: 0;
		height: 65%;
		transform: scaleX(-1);
	}
	.rose-orn {
		position: absolute;
		left: 0;
		bottom: 2rem;
		width: 260px;
		transform: translate(-50%, 0);
		@include max-md {
			width: 200px;
		}
		img {
			&:nth-child(1) {
				position: absolute;
				right: 20%;
				bottom: 5px;
				z-index: -1;
				transform-origin: 70% 100%;
				width: 90%;
			}
			&:nth-child(2) {
				width: 80%;
				transform-origin: 70% 100%;
			}
		}
	}
	.image-wrap {
		position: relative;
		width: 100%;
		height: 480px;
		&::after {
			position: absolute;
			content: "";
			bottom: 0;
			left: 0;
			width: 100%;
			height: 45%;
			background: linear-gradient(to top, #fff 0%, transparent 100%);
		}
		.gift-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.gift-wrap {
		position: relative;
		padding: 3rem 2rem;
		background-color: #fff;
		border-radius: 15px;
		margin-bottom: 2rem;
		color: var(--color-dark);
		@include max-md {
			padding: 2rem 1rem;
		}
	}
	.tabs-gift {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: auto;
		width: fit-content;
		padding: 0.25rem;
		border-radius: 10px;
		gap: 0.5rem;
		&::before {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: var(--color-secondary);
			opacity: 0.25;
			content: "";
			border-radius: 10px;
		}
		@include max-md {
			padding: 0.25rem;
		}
		.btn-custom {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 0.5rem;
			min-width: 140px;
			text-align: center;
			background-color: transparent;
			color: rgba($color: #000, $alpha: 0.5);
			z-index: 2;
			@include max-md {
				min-width: 120px;
				svg {
					width: 25px;
					height: 25px;
				}
			}
			path {
				fill: rgba($color: #000, $alpha: 0.5);
				transition: all 0.2s ease;
			}
			&.active {
				color: #fff;
				path {
					fill: #fff;
				}
			}
			&:focus {
				box-shadow: none;
			}
		}
		.glider {
			position: absolute;
			left: 0;
			top: 0;
			background: var(--color-secondary);
			width: 100%;
			height: 100%;
			border-radius: 10px;
			z-index: -1;
		}
	}
	.card {
		.card-body {
			padding: 3rem;
			@include max-md {
				padding: 3rem 1.25rem;
			}
			@include max-xs {
				padding: 3rem 0.75rem;
			}
		}
	}
	form {
		.input-group-text {
			background-color: #fff;
			border-color: rgba($color: #000, $alpha: 0.25);
		}
		.btn-custom {
			background: linear-gradient(to right, #d6b540 0%, #fff2c2 100%);
		}
		@include max-md {
			.row {
				gap: 1rem;
			}
		}
	}
	.gift,
	.angpao {
		display: none;
		transition: all 0.5s ease-in;
		opacity: 0;
		&.show {
			display: block;
			opacity: 1;
		}
	}
	.gift-wrapper {
		position: relative;
		background-color: #fff;
		margin-top: 2rem;
		border-radius: 8px;
		height: 100%;
		.gift-content {
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			text-align: left;
			height: 100%;
			gap: 1rem;
			.gift-image-wrap {
				position: relative;
				width: 100%;
				height: 250px;
				margin-bottom: 0.75rem;
				.gift-image-item {
					width: 100%;
					height: 100%;
					object-fit: cover;
					border-radius: 10px;
				}
			}
			.gift-description {
				position: relative;
				padding: 0 15px;
			}
			.gift-action {
				display: flex;
				flex-direction: column;
				gap: 0.75rem;
				padding: 0 15px 15px;
			}
		}
	}
}

@keyframes tab {
	0% {
		transform: scale(0.5);
	}
	100% {
		transform: scale(1);
	}
}
