.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.image-wrapper {
		position: relative;
		height: 100%;
		width: fit-content;
		border-radius: 1000px 50px 50px 50px;
		background-color: var(--color-gradient);
		padding: 4px;
		margin: -3rem -4rem 0 0;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			height: 100%;
			width: 100%;
			content: "";
			background: linear-gradient(to bottom, #d6b540 0%, #fff2c2 100%);
			border-radius: 1000px 50px 50px 50px;
			z-index: 8;
		}
		@include max-md {
			margin: 0 0 -5.5rem 0;
			height: 320px;
		}
		.rsvp-background {
			height: 100%;
			max-width: 100%;
			object-fit: cover;
			border-radius: 1000px 50px 50px 50px;
			position: relative;
			z-index: 10;
		}
	}
	.border-gradient {
		padding: 4px;
		position: relative;
		&::before {
			background-color: var(--color-gradient);
			border-radius: 15px;
			content: "";
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 2;
		}
	}
	.rose-orn {
		position: absolute;
		bottom: 0;
		left: 0;
		transform: translate(-50%, 50%) scaleY(-1);
		width: 180px;
		@include max-md {
			width: 140px;
		}
		img {
			transform-origin: 70% 90%;
		}
	}
	.daun-orn {
		position: absolute;
		top: 0;
		left: 0;
		transform: translateX(-40%);
		width: 240px;
		img {
			transform-origin: 70% 100%;
		}
	}
	.rsvp-wrap {
		padding: 3rem 3rem 3rem 6rem;
		border-radius: 15px;
		background: var(--color-primary);
		margin-bottom: -3rem;
		position: relative;
		z-index: 5;
		@include max-lg {
			padding: 3rem 3rem 3rem 4rem;
		}
		@include max-md {
			padding: 8rem 1.5rem 2rem;
			margin-bottom: 0;
		}
		.btn-custom {
			background: linear-gradient(to right, #d6b540 0%, #fff2c2 100%);
		}
	}
}
