.logo-viding {
	height: 2.4rem;
	path {
		fill: #fff;
	}
	@include max-md {
		width: 2.25rem;
	}
}

.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	background-color: var(--color-primary);
	border-radius: 50%/12%;
	border-top-right-radius: 0;
	border-top-left-radius: 0;
	@include max-md {
		border-radius: 50%/2%;
		border-top-right-radius: 0;
		border-top-left-radius: 0;
		.row {
			height: auto !important;
		}
	}
	overflow: hidden;
	@include max-md {
		.h-sm-auto {
			height: auto !important;
		}
	}
	.rose-bg-orn {
		height: 100%;
		right: 0;
		top: 0;
		position: absolute;
	}
	.image-wrapper {
		position: relative;
		height: 70%;
		width: fit-content;
		padding: 4px;
		transform-origin: 100% 100%;
		@include min-md {
			margin-left: auto;
		}
		@include max-lg {
			height: 65%;
		}
		@include max-md {
			width: 70%;
			height: 320px;
			margin: 0 auto;
		}
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			height: 100%;
			width: 100%;
			content: "";
			background: linear-gradient(to bottom, #d6b540 0%, #fff2c2 100%);
			border-radius: 1000px 50px 50px 50px;
			z-index: 2;
		}
		.rose-orn {
			position: absolute;
			bottom: 0;
			left: 0;
			transform: translateX(-60%);
			width: 240px;
			@include max-md {
				width: 180px;
			}
			img {
				transform-origin: 80% 100%;
				&:nth-child(2) {
					position: absolute;
					right: 10%;
					bottom: 0;
					width: 70%;
				}
			}
		}
		.background-header {
			height: 100%;
			max-width: 100%;
			object-fit: cover;
			border-radius: 1000px 50px 50px 50px;
			position: relative;
			z-index: 5;
			@include max-md {
				width: 100%;
			}
		}
	}
	.header-content {
		position: relative;
		text-align: left;
		padding: 4rem 0 0;
		@include max-md {
			padding: 5rem 0 0;
			text-align: center;
		}
		.header {
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			padding: 2rem 0 3rem;
			position: relative;
			z-index: 3;
			@include max-md {
				padding-bottom: 0;
			}
			.header-title {
				color: #fff;
				position: relative;
				width: 100%;
				margin-top: -2rem;
				@include max-md {
					margin-top: 0;
				}
				.title {
					color: #fff;
					transform-origin: 0% 0%;
				}
				.header-description {
					transform-origin: 0% 0%;
				}
			}
		}
	}
}

.envelope-wrap {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	color: #fff;
	border-radius: 0;
	.image-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: rgba($color: #000000, $alpha: 0.35);
			content: "";
			border-radius: 0;
			z-index: 10;
		}
		.background-header {
			width: 100%;
			height: 100%;
			border-radius: 0;
		}
	}
	.header-content {
		z-index: 20;
		.header {
			justify-content: space-between;
			padding: 3rem 0 4rem;
			@include max-md {
				padding: 3rem 0 5rem;
			}
			.header-title {
				margin-bottom: 0;
				padding: 3rem;
				text-align: center;
				@include max-md {
					padding: 3rem 0.75rem;
				}
			}
		}
	}
}
