.stream-section {
	position: relative;
	padding: 3rem 0;
	text-align: center;
	@include max-lg {
		.row {
			gap: 1.5rem;
		}
	}
	.rose-orn {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, -40%);
		@include max-md {
			width: 140px;
		}
		img {
			transform-origin: 70% 90%;
		}
	}
	.daun-orn {
		position: absolute;
		top: 0;
		right: 0;
		width: 240px;
		transform: translate(40%, -40%) scaleX(-1);
		@include max-md {
			width: 220px;
		}
		img {
			transform-origin: 70% 100%;
		}
	}
	.background-stream {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 80%;
		content: "";
		background-color: var(--color-primary);
		border-radius: 50%/12%;
		@include max-md {
			border-radius: 50%/2%;
		}
	}
	.stream-wrapper {
		position: relative;
		background-color: var(--color-primary);
		padding: 3rem 2rem;
		border-radius: 15px;
		margin-bottom: 3rem;
		@include max-md {
			padding: 2rem 1rem;
		}
		h4 {
			@include max-md {
				text-align: center;
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.stream-wrap {
		background-color: #fff;
		border-radius: 15px;
		padding: 3rem 0;
		position: relative;
		@include max-md {
			padding: 3rem 1rem;
		}
	}
	.btn-custom {
		width: 50%;
		@include max-lg {
			width: 100%;
		}
		@include max-md {
			width: fit-content;
		}
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		@include max-md {
			text-align: center;
		}
	}
}
