.venue-section {
	position: relative;
	padding: 5rem 0 8rem;
	@include max-md {
		padding: 3rem 0 5rem;
	}
	.background-venue {
		position: absolute;
		width: 100%;
		height: 120%;
		background-color: var(--color-primary);
		left: 0;
		bottom: 0;
		border-radius: 50%/12%;
		@include max-md {
			border-radius: 50%/2%;
		}
	}
	.rose-bg-trans {
		position: absolute;
		right: 0;
		bottom: 8rem;
		height: 80%;
		transform: scaleX(-1);
	}
	@include max-md {
		.rose-bg-trans {
			bottom: 5rem;
		}
	}
	.venue-wrapper {
		position: relative;
		.venue-content {
			position: relative;
			text-align: center;
			margin-top: 5rem;
			.rose-orn {
				position: absolute;
				top: 0;
				left: 50%;
				width: 140px;
				transform: translate(-50%, -50%);
				@include max-md {
					width: 120px;
				}
				&.second {
					transform: translate(-50%, -50%) scaleX(-1);
				}
				img {
					transform-origin: 70% 90%;
				}
			}
			.card {
				border: none;
				background-color: #fff;
				height: 100%;
				border-radius: 15px;
				.card-body {
					padding: 3rem 2rem;
					color: var(--color-dark);
					@include max-md {
						padding: 2rem 0.75rem;
					}
				}
			}
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
		}
	}
	.row-venue {
		@include max-md {
			gap: 2rem;
		}
	}
	.event-name {
		position: relative;
		.date {
			display: flex;
			align-items: center;
			gap: 0.5rem;
			text-align: left;
			h5 {
				font-weight: 700;
				margin-bottom: 0;
				font-size: 38px;
			}
			p {
				margin-top: 0.2rem;
				font-size: 12px;
			}
		}
		p {
			margin-bottom: 0;
		}
	}
	.btn-custom {
		background-color: transparent;
		border-color: var(--color-dark);
		color: var(--color-dark);
		transition: all 0.25 ease-in-out;
		&:hover {
			background-color: var(--color-secondary);
			border-color: var(--color-secondary);
			color: #fff;
		}
	}
}

.maps-embed {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
