.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	color: #ffff;
	@include max-md {
		padding: 3rem 0;
	}
	.rose-orn {
		position: absolute;
		bottom: 2rem;
		left: 0;
		width: 180px;
		transform: translateX(-40%);
		@include max-md {
			bottom: 0;
			width: 140px;
			transform: translate(-40%, 100%);
			z-index: 2;
		}
		img {
			transform-origin: 70% 100%;
		}
	}
	.daun-orn {
		position: absolute;
		top: 0;
		right: 0;
		width: 240px;
		transform: translate(40%, -40%) scaleX(-1);
		img {
			transform-origin: 70% 100%;
		}
	}
	.image-wrapper {
		position: absolute;
		height: 120%;
		width: fit-content;
		margin-right: -3rem;
		background: var(--color-gradient);
		padding: 4px;
		z-index: 3;
		top: 50%;
		left: 0;
		transform: translateY(-50%);
		border-radius: 1000px 50px 50px 50px;
		@include max-md {
			width: 100%;
			height: 320px;
			margin-right: 0;
			margin-bottom: -10rem;
			position: relative;
			transform: none;
			inset: 0;
		}
		.thank-background {
			height: 100%;
			max-width: 100%;
			object-fit: cover;
			border-radius: 1000px 50px 50px 50px;
		}
	}
	.thank {
		position: relative;
		text-align: center;
		padding: 3rem 2rem;
		z-index: 3;
		background: var(--color-gradient);
		border-radius: 15px;
		color: var(--color-dark);
		&::before {
			position: absolute;
			top: 4px;
			right: 4px;
			bottom: 4px;
			left: 4px;
			content: "";
			background-color: #fff;
			border-radius: 15px;
		}
		* {
			position: relative;
		}
		@include max-md {
			border-width: 2px;
			padding: 14rem 1rem 2rem;
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background: var(--color-primary);
	small {
		position: relative;
	}
}
