.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.story-content-wrap {
		position: relative;
		text-align: center;
		padding: 4px;
		background: var(--color-gradient);
		border-radius: 15px;
		z-index: 10;
		.story-content {
			position: relative;
			background-color: var(--color-primary);
			border-radius: 15px;
			padding: 14rem 2rem 3rem;
			* {
				position: relative;
			}
		}
		.rose-bg-trans {
			position: absolute;
			left: 0;
			bottom: 0;
			height: 480px;
		}
	}
	.image-story-wrap {
		position: relative;
		height: 580px;
		width: 100%;
		border-radius: 1000px 1000px 15px 15px;
		margin-bottom: -10rem;
		background: var(--color-gradient);
		padding: 4px;
		z-index: 12;
		@include max-md {
			height: 360px;
		}
		.story-image {
			border-radius: 1000px 1000px 15px 15px;
			object-fit: cover;
			width: 100%;
			height: 100%;
			position: relative;
		}
	}
}
