.wishes-section {
	position: relative;
	padding: 5rem 0 0;
	@include max-md {
		padding: 3rem 0 0;
	}

	.rose-orn {
		position: absolute;
		right: 0;
		top: 2rem;
		width: 260px;
		transform: translate(40%, 0) scaleX(-1);
		@include max-md {
			width: 200px;
		}
		img {
			&:nth-child(1) {
				position: absolute;
				left: -20%;
				bottom: 5px;
				z-index: -1;
				transform-origin: 70% 100%;
				width: 90%;
			}
			&:nth-child(2) {
				width: 70%;
				transform-origin: 70% 100%;
			}
		}
	}

	.card-form {
		margin-bottom: 0;
		background-color: #fff;
		position: relative;
		z-index: 3;
		border-radius: 15px;
		&.card {
			border: none;
		}
		.card-body {
			padding: 2rem 1rem;
			@include min-lg {
				padding: 3rem;
			}
		}
	}

	.wishes-preview {
		color: #fff;
		position: relative;
		padding: 10rem 0 2rem;
		background-color: var(--color-primary);
		overflow: hidden;
		margin-top: -10rem;
		border-radius: 50%/12%;
		@include max-md {
			border-radius: 50%/2%;
		}
		.rose-bg-trans {
			position: absolute;
			right: 0;
			bottom: 0;
			height: 100%;
			transform: scaleX(-1);
		}
		.card {
			border: none;
			position: relative;
			background-color: transparent;
			.card-body {
				overflow: hidden;
				@include min-lg {
					padding: 3rem;
				}
				.wishes {
					text-align: left;
					height: 23.05rem;
					overflow: auto;
					&::-webkit-scrollbar {
						display: none;
					}
					.text-muted {
						color: rgba($color: #fff, $alpha: 0.8) !important;
					}
					.wish {
						margin-bottom: 1.5rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
					}
				}
			}
		}
	}
}
