.couple-section {
	position: relative;
	padding: 3rem 0 0;
	text-align: center;
	.rose-orn-couple-man {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 240px;
		transform: translateX(-50%);
		@include max-md {
			width: 200px;
			bottom: unset;
			top: 3rem;
		}
		img {
			transform-origin: 80% 100%;
			width: 110%;
			&:nth-child(2) {
				position: absolute;
				bottom: 0;
				right: 10%;
				width: 70%;
			}
		}
	}
	.rose-orn-couple-woman {
		position: absolute;
		right: 0;
		bottom: 0;
		transform: translateY(50%);
		width: 260px;
		@include max-md {
			width: 200px;
			transform: translate(40%, 50%);
		}
		img {
			&:nth-child(1) {
				position: absolute;
				right: -20%;
				bottom: 85%;
				width: 60%;
				transform-origin: 20% 100%;
				@include max-md {
					left: unset;
					right: 3rem;
					width: 50%;
					bottom: 2rem;
				}
			}
			&:nth-child(2) {
				transform-origin: 50% 20%;
				width: 60%;
			}
		}
	}
	.and {
		margin: 1.5rem 0;
		font-weight: 600;
		color: var(--color-dark);
	}
	.btn-reminder {
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.25rem;
		transition: all 0.2s ease-in-out;
		path {
			transition: all 0.3s ease-in-out;
		}
	}
	.couple-content-wrap {
		background: linear-gradient(to bottom, #d6b540 0%, #fff2c2 100%);
		border-radius: 15px;
		position: relative;
		padding: 4px;
		margin-top: -8rem;
		z-index: 5;
		.couple-content {
			position: relative;
			background-color: var(--color-primary);
			border-radius: 15px;
			padding: 3rem 1.5rem;
			.rose-bg-orn-2 {
				position: absolute;
				left: 0;
				bottom: 0;
				height: 100%;
			}
		}
	}
	.couple {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		@include max-md {
			flex-direction: column;
			&.flex-reverse-sm {
				flex-direction: column-reverse;
			}
		}
		.couple-description {
			position: relative;
			background-color: var(--color-primary);
			border-radius: 10px;
			width: 100%;
			@include max-md {
				padding: 120px 0.75rem 1.5rem !important;
				margin: -100px 0 0 !important;
			}
			&.man {
				padding: 0.75rem 2rem 0.75rem 120px;
				margin-left: -100px;
				h3,
				.couple-parent {
					transform-origin: 0% 0%;
				}
			}
			&.woman {
				padding: 0.75rem 120px 0.75rem 2rem;
				margin-right: -100px;
				h3,
				.couple-parent {
					transform-origin: 100% 100%;
				}
			}
			.rose-orn-trans-man {
				position: absolute;
				right: 0;
				top: 0;
				height: 100%;
				transform: scaleX(-1);
			}
			.rose-orn-trans-woman {
				position: absolute;
				left: 0;
				top: 0;
				height: 100%;
			}
			[data-anim="rotate-right"] {
				transform-origin: 100% 0%;
			}
		}
		.image-wrap {
			width: 380px;
			aspect-ratio: 1;
			position: relative;
			z-index: 3;
			padding: 4px;
			transform-origin: 100% 100%;
			&::before {
				position: absolute;
				border-radius: 1000px 50px 50px 50px;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				content: "";
				background: linear-gradient(to bottom, #d6b540 0%, #fff2c2 100%);
				z-index: 3;
			}
			&.woman {
				border-radius: 50px 1000px 50px 50px;
				transform-origin: 0% 100%;
				&::before {
					position: absolute;
					border-radius: 50px 1000px 50px 50px;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					content: "";
					background: linear-gradient(to bottom, #d6b540 0%, #fff2c2 100%);
					z-index: 3;
				}
			}
			@include max-md {
				width: 80%;
				height: 320px;
			}
			.couple-image {
				height: 100%;
				object-fit: cover;
				width: 100%;
				border-radius: 1000px 50px 50px 50px;
				position: relative;
				z-index: 5;
				&.woman {
					border-radius: 50px 1000px 50px 50px;
				}
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: absolute;
			left: 120px;
			bottom: 0;
			transform: translateY(50%);
			@include max-md {
				left: 50%;
				transform: translate(-50%, 50%);
			}
			&.right {
				left: unset;
				right: 120px;
				@include max-md {
					left: 50%;
					right: unset;
				}
			}
			.sosmed {
				color: var(--color-dark);
				padding: 0.5rem 1rem;
				border-radius: 15px;
				background-color: #fff;
				text-decoration: none;
				display: block;
				small {
					display: flex;
					align-items: center;
					gap: 0.25rem;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
	.reminder-wrap {
		position: relative;
		padding: 5rem 0;
		margin-top: 2rem;
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		@include max-md {
			flex-direction: column;
			padding: 3rem 0;
		}
		.btn-reminder {
			z-index: 3;
			position: relative;
			margin: 0;
			margin-top: 2rem;
			@include max-md {
				margin-top: 1rem;
			}
		}
	}
}
